/* Default size (small) */
.progressContainer {
  width: 99px;  
  height: 90px; 
  background-color: transparent;
  position: relative;
  margin-bottom: 1rem;  
}

.outerBar {
  width: 90px; 
  height: 50px;
  border-radius: 50% 50% 0 0 / 100% 100% 0 0;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.innerBar {
  background-color: transparent;
  position: relative;
  z-index: 500;
  width: 70px;  
  height: 40px;
  border-radius: 52% 50% 0 0 / 100% 100% 0 0;
  display: flex;
  justify-content: center;
}

.progressContent {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.svg {
  position: absolute;
  width: 90px;  
  height: 80px; 
  transform: rotate(0deg);
  top: -10px;   
  left: 0;
}

.path {
  fill: none;
  stroke-width: 20px; 
}

.progressTitle {
  margin: 10px 0;  
  margin-top: 6rem;
  font-weight: 600;
  font-size: 13px;
  text-align: center;
  color: #b0b0b0;
}

.progressSubtitle {
  padding: 0;
  margin-top: 0;
  font-size: 10px; 
  font-weight: 400;
  color: #b0b0b0;
  text-align: center;
}

/* Large size */
.large .progressContainer {
  width: 109px;
  height: 100px;
}
.large .outerBar {
  width: 100px;
  height: 60px;
}
.large .innerBar {
  width: 80px;
  height: 50px;
}
.large .svg {
  width: 100px;
  height: 90px;
}
.large .path {
  stroke-width: 30px;
}
.large .progressTitle {
  font-size: 15px;
}
.large .progressSubtitle {
  font-size: 12px;
}
